*
{
  margin: 0;
  padding: 0;
}

body
{
  color: #fff;
 background-color: rgb(0, 0, 0);
}

.app
{
  text-align: center;
  color: rgb(0, 255, 0);
  
}

.h1
{
  color: green;
}
.app-input input{
  font-size: 25px;
  border-radius: 5px;
  border: 1px solid grey;
  outline: none;
  padding: 10px 5px;
  width: 200px;

}
.app-container{
  padding: 50px;
  color: #fff;
}
.il
{
  background-color: black;
  color: white;
  border: 2px solid#fff;
 
  width: 300px;
  height: 30px;

}

.app button
{
  background-color: rgb(30, 255, 0);
  color: rgb(0, 0, 0);
  border: none;
  margin-top: 10px;
  border-radius: 20px;
  padding: 10px  30px;
  font-size: 20px;
  cursor: pointer;
  margin-bottom: 30px;
}

.navActive
{
  color: rgb(255, 255, 255);
  font-size: 30px;
  font-weight: bold;
  
}

.nav
{
  background-color: rgb(64, 226, 43);
  padding:10px;
  display: flex;
  justify-content: space-around;
  margin-top: 5px;
  
}

.nav ul
{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  color: rgb(0, 0, 0);
  font-weight: bolder;
  cursor: pointer;

}

.selectednav
{
  margin-left: -35px;
}
.nav ul li{
  transition: all 0.5s ease-in-out;
  cursor:pointer;
  font-size: 25px;

}
.nav ul li:hover
{
  transform: scale(0.8);
  color: red;
}
@media (max-width: 600px) {
.nav
{
  background-color: rgb(64, 226, 43);
  position: absolute;
  left: 0;
  right: 0;
 
 
  
}
  
  .nav ul
  {
    
    display: flex;
    list-style-type: none;
align-items: start;
    width: 40%;
    color: rgb(0, 0, 0);
    font-weight: bolder;
    cursor: pointer;
    margin-right: 100px;
  
  }
  .nav ul 
li{
  margin-left: 0px;
  margin-right: 20px;
}
}